body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right top, left top, from(#071b52), to(teal));
  background: linear-gradient(to left, #071b52 0%, teal 100%);
  height: 100%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

/* #### Generated By: http://www.cufonfonts.com #### */
@font-face {
  font-family: "SEGA LOGO FONT";
  font-style: normal;
  font-weight: normal;
  src: local("SEGA LOGO FONT"), url(/robofriends/static/media/SEGA.12a3db8b.woff) format("woff"); }

h1 {
  font-family: "SEGA LOGO FONT";
  color: #0ccac4;
  font-weight: 200; }

.card img {
  height: 250px;
  width: 250px; }

